<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <div class="main">
                <el-row>
                    <el-col :span='18' :offset='3'>
                        <div class="basic">
                            <el-row type='flex' justify="space-around">
                                <el-col :span='3' v-for='(i,index) in basic_list' :key='index'>
                                    <div class="basic_t" :class='num==index?"active":""'>
                                        <div>
                                            <i class='iconfont icon-dui'></i>
                                        </div>
                                        <span>
                                            {{i.text}}
                                        </span>
                                    </div>
                                </el-col>
                            </el-row>
                        </div>
                    </el-col>
                </el-row>
            </div>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <!-- 基本信息 -->
                <div class="basic_content" v-if='num==0'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>基本信息</span>
                                <span class="r">
                                    <span class='rs'>*</span>
                                    <span>必填项</span>
                                </span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='12'>
                                        <el-form ref="form" :model="form" label-width="100px" :rules="rules">
                                            <el-form-item label="产品类型：" prop='product_t'>
                                                <el-select v-model="form.product_t" placeholder="请选择" style='width:100%;'>
                                                    <el-option v-for="(i,index) in options" :key="index" :label="i.typeName" :value="i.id">
                                                    </el-option>
                                                </el-select>
                                            </el-form-item>
                                            <el-form-item label="产品名称：" class='is-required' prop='name'>
                                                <el-input v-model="form.name" maxlength='10'></el-input>
                                            </el-form-item>
                                            <el-form-item label="产品图片：" prop='image'>
                                                <div class="files">+
                                                    <div class="files_left">
                                                        <img :src="form.image" alt="" style='width: 100px;height: 100px;'>
                                                    </div>
                                                    <div class="files_right">
                                                        <input type="file" class="form-control" name='upload' @change="onFileChange">
                                                    </div>
                                                </div>
                                            </el-form-item>
                                            <el-form-item label="产品描述：" class='is-required' prop='text'>
                                                <el-input type="textarea" v-model="form.text" :rows='12' maxlength="500" show-word-limit></el-input>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='next1("form")'>下一步</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <!-- 产品特点 -->
                <div class="basic_content" v-if='num==1'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>产品特点</span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='20'>
                                        <el-form :model="form2" label-width="100px">
                                            <el-form-item label="产品特点">
                                                <div style="height:500px;width:100%;">
                                                    <editor-bar v-model="detail" @change="change1"></editor-bar>
                                                </div>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='next'>下一步</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <!-- 技术参数 -->
                <div class="basic_content" v-if='num==2'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>技术参数</span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <div class="js_list">
                            <el-row>
                                <el-form ref="form" :model="form" label-width="100px" :rules="rules">
                                    <el-form-item label="产品图片：" prop='image2'>
                                        <div class="files">+
                                            <div class="files_left">
                                                <img :src="form.image2" alt="" style='width: 100px;height: 100px;' id="image">
                                            </div>
                                            <div class="files_right">
                                                <input type="file" class="form-control" name='upload' @change="onFileChange2">
                                            </div>
                                        </div>
                                    </el-form-item>
                                    <el-form-item>
                                        <el-button type="primary" @click='next2("form")'>下一步</el-button>
                                    </el-form-item>
                                </el-form>
                            </el-row>
                        </div>
                    </el-row>
                </div>
                <!-- 外观尺寸 -->
                <div class="basic_content" v-if='num==3'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>外观尺寸</span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <div class="wg_list">
                            <el-row>
                                <el-form ref="form" :model="form" label-width="100px" :rules="rules">
                                    <el-form-item label="产品图片：" prop='image4'>
                                        <div class="files">+
                                            <div class="files_left">
                                                <img :src="form.image4" alt="" style='width: 100px;height: 100px;' id="image">
                                            </div>
                                            <div class="files_right">
                                                <input type="file" class="form-control" name='upload2' @change="onFileChange3">
                                            </div>
                                        </div>
                                    </el-form-item>
                                    <el-form-item>
                                        <el-button type="primary" @click='next2("form")'>下一步</el-button>
                                    </el-form-item>
                                </el-form>
                            </el-row>
                        </div>
                    </el-row>
                </div>
                <!-- 订货须知 -->
                <div class="basic_content" v-if='num==4'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>订货须知</span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='20'>
                                        <el-form :model="form2" label-width="100px">
                                            <el-form-item label="订货须知">
                                                <div style="height:500px;">
                                                    <editor-bar v-model="ordering" :isClear="isClear" @change="change2"></editor-bar>
                                                </div>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='next'>下一步</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <!-- 注意事项 -->
                <div class="basic_content" v-if='num==5'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>注意事项</span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='20'>
                                        <el-form :model="form2" label-width="100px">
                                            <el-form-item label="注意事项">
                                                <div style="height:500px;">
                                                    <editor-bar v-model="attention" :isClear="isClear" @change="change3"></editor-bar>
                                                </div>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='save'>保存草稿</el-button>
                                                <el-button type="primary" @click='sub'>发布</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
import EditorBar from '../../components/wangEnduit.vue'
// import EditableCell from "../../components/EditeableCell.vue"
export default {
    components: {
        headers,
        EditorBar
    },
    data() {
        return {
            file: '',
            file2: '',
            file3: '',
            editModeEnabled: false,
            isClear: false,
            detail: "", //产品特点
            ordering: "", //订货须知
            attention: "", //注意事项
            num: 0,
            title: '新增产品',
            basic_list: [{
                text: '基本信息'
            }, {
                text: '产品特点'
            }, {
                text: '技术参数'
            }, {
                text: '外观尺寸'
            }, {
                text: '订货须知'
            }, {
                text: '注意事项'
            }],
            form: {
                name: '',
                product_t: '',
                text: '',
                image: '',
                image2: '',
                image4: ''
            },
            form2: {
                text: ''
            },
            image: '',
            image2: '',
            image3: '',
            rules: {
                name: [{
                    required: true,
                    message: '产品名称不能为空'
                }],
                text: [{
                    required: true,
                    message: '产品描述不能为空'
                }, {
                    min: 1,
                    max: 1000,
                    message: '不能超过1000字'
                }],
                product_t: [{
                    required: true,
                    message: '请选择产品类型',
                    trigger: 'change'
                }],
                image: [{
                    required: true,
                    message: '请选择图片'
                }],
                image2: [{
                    required: true,
                    message: '请选择图片'
                }],
                image4: [{
                    required: true,
                    message: '请选择图片'
                }]
            },
            options: [],
            /*技术参数*/
            skll_list: [{
                nominal: '1',
                model: '1',
                traffic: '1',
                initiation: '1',
                pressure: '1',
                accuracy: '1',
                qmax: '1',
                shell: '1'
            }],
            /*外观尺寸*/
            appearance_list: [{
                nominal: '2',
                model: '2',
                traffic: '2',
                initiation: '2',
                pressure: '2',
                accuracy: '2',
                qmax: '2',
                shell: '2'
            }]
        }
    },
    methods: {
        fn(index) {
            this.num = index
            var a = document.querySelectorAll('.basic_t div')
            a[index].setAttribute('class', 'active')
        },
        next1(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.num += 1
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        next() {
            this.num += 1
            console.log(this.num)
        },
        next2(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.num += 1
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        /*产品特点*/
        change1(val) {
            // console.log(val)
            this.detail = val
        },
        /*订货须知*/
        change2(val) {
            // console.log(val)
            this.ordering = val
        },
        /*注意事项*/
        change3(val) {
            // console.log(val)
            this.attention = val
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#f5f5f5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        onFileChange3(e) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;
            this.createImage3(files[0]);
            this.file3 = files[0]
            console.log(this.file,1)
        },
        createImage3(file) {
            var image = new Image();
            var reader = new FileReader();
            var vm = this;
            reader.onload = (e) => {
                vm.form.image4 = e.target.result;
            };
            reader.readAsDataURL(file);
            console.log(vm.form.image4)
            setTimeout(() => {
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        // alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                })
            }, 100)
        },
        onFileChange2(e) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;
            this.createImage2(files[0]);
            this.file2 = files[0]
            console.log(this.file)
        },
        createImage2(file) {
            var image = new Image();
            var reader = new FileReader();
            var vm = this;
            reader.onload = (e) => {
                vm.form.image2 = e.target.result;
            };
            reader.readAsDataURL(file);
            setTimeout(() => {
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        // alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                })
            }, 100)
        },
        onFileChange(e) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;
            this.createImage(files[0]);
            this.file = files[0]
            console.log(this.file)
        },
        createImage(file) {
            var image = new Image();
            var reader = new FileReader();
            var vm = this;
            reader.onload = (e) => {
                vm.form.image = e.target.result;
            };
            reader.readAsDataURL(file);
            setTimeout(() => {
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        // alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                })
            }, 100)
        },
        onSubmit() {
            console.log('submit!');
        },
        sub() {
            this.$confirm('确认发布, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    var id = window.sessionStorage.getItem('userId')
                    var formData = new window.FormData()
                    formData.append('file', this.file)
                    formData.append('name', this.form.name)
                    formData.append('productTypeId', this.form.product_t)
                    formData.append('context', this.form.text)
                    formData.append('productTrait', this.detail) //产品特点
                    //formData.append('productParameters', JSON.stringify(this.skll_list)) //技术参数
                    //formData.append('productExternal', JSON.stringify(this.appearance_list)) //外观尺寸
                    formData.append('productParameters', this.file2) //技术参数
                    formData.append('productExternal', this.file3) //外观尺寸
                    formData.append('productOrder', this.ordering) //订货须知
                    formData.append('productAttention', this.attention) //注意事项
                    formData.append('userId', id)
                    this.$axios.post("/tomlive/productInfo/insertSelectProductInfo", formData, {
                        headers: {
                            "Content-Type": "multipart/form-data",
                        }
                    }).then((res) => {
                        console.log(res)
                        this.$router.push('/home/product_fb_c')
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消添加'
                });
            })
        },
        save() {
            this.$confirm('确认保存, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    var id = window.sessionStorage.getItem('userId')
                    var formData = new window.FormData()
                    formData.append('file', this.file)
                    formData.append('name', this.form.name)
                    formData.append('productTypeId', this.form.product_t)
                    formData.append('context', this.form.text)
                    formData.append('productTrait', this.detail) //产品特点
                    //formData.append('productParameters', JSON.stringify(this.skll_list)) //技术参数
                    //formData.append('productExternal', JSON.stringify(this.appearance_list)) //外观尺寸
                    formData.append('productParameters', this.file2) //技术参数
                    formData.append('productExternal', this.file3) //外观尺寸
                    formData.append('productOrder', this.ordering) //订货须知
                    formData.append('productAttention', this.attention) //注意事项
                    formData.append('userId', id)
                    this.$axios.post("/tomlive/productInfo/insertSelectProductInfo1", formData, {
                        headers: {
                            "Content-Type": "multipart/form-data",
                        }
                    }).then((res) => {
                        console.log(res)
                        this.$router.push('/home/product_add_c')
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消添加'
                });
            })
            /*var id = window.sessionStorage.getItem('userId')
            var formData = new window.FormData()
            formData.append('file', this.file)
            formData.append('name', this.form.name)
            formData.append('productTypeId', this.form.product_t)
            formData.append('context', this.form.text)
            formData.append('productTrait', this.detail) //产品特点
            formData.append('productParameters', JSON.stringify(this.skll_list)) //技术参数
            formData.append('productExternal', JSON.stringify(this.appearance_list)) //外观尺寸
            formData.append('productOrder', this.ordering) //订货须知
            formData.append('productAttention', this.attention) //注意事项
            formData.append('userId', id)
            this.$axios.post("/tomlive/productInfo/insertSelectProductInfo1", formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                }
            }).then((res) => {
                console.log(res)
                this.$router.push('/home/product_add_c')
            })*/
        }
    },
    mounted() {
        this.$axios.post('/tomlive/productType/selectAllProductType').then((res) => {
            console.log(res)
            /*var arr = res.data.info;
            var arr2 = [];
            for (var i = 0; i < arr.length; i++) {
                arr2.push(arr[i].typeName)
            }
            this.options = [...new Set(arr2)]*/
            this.options = res.data.info;
        })
    }
}
</script>
<style scoped>
#editor {
    width: 100%;
    height: 500px;
}

.main {
    width: 100%;
    height: auto;
}

.basic {
    width: 100%;
    height: 170px;
    background: #fff;
    box-sizing: border-box;
    margin: 20px 0 0 0;
    border: 1px solid #e9e9e9;
}

.basic_content {
    width: 100%;
    height: 920px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    margin-top: 40px;
    background: #fff;
}

.active {
    color: rgba(0, 121, 254, 1);
}

.basic_t {
    width: 100%;
    height: 150px;
    text-align: center;
    cursor: pointer;
}

.basic_t>div {
    width: 100%;
    height: 100px;
    line-height: 100px;
}

.basic_t>div>i {
    font-size: 36px;
}

.basic_t>span {
    font-size: 14px;
    font-weight: bold;
    color: #666;
}

.basic_title {
    width: 100%;
    height: 50px;
    color: #666;
    font-size: 14px;
    line-height: 50px;
    font-weight: bold;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.basic_title>span {
    margin: 0 20px;
}

.basic_exit {
    width: 100%;
    margin: 40px 0;
}

.r {
    float: right;
}

.r>.rs {
    color: red;
    margin: 0 5px;
}

.js_list {
    padding: 20px;
    min-height: 500px;
}

.wg_list {
    padding: 20px;
    min-height: 500px;
}

.files {
    width: 100px;
    height: 100px;
    position: relative;
    border: 1px solid #e9e9e9;
    font-size: 66px;
    font-weight: 1;
    color: #e9e9e9;
    text-align: center;
    line-height: 100px;
}

.files>img {
    width: 100px;
    height: 100px;
}

.files_left {
    width: 100px;
    height: 100px;
    position: absolute;
    left: 0;
    top: 0;
}

.files_left>img {
    width: 100px;
    height: 100px;
}

.files_right {
    width: 100px;
    height: 100px;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 999;
}

.files_right>input {
    width: 100%;
    height: 100px;
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 999;
}
</style>
<style>
.el-textarea .el-textarea__inner {
    resize: none;
}

.tb-edit .input-box {
    display: none
}

.tb-edit .current-cell .input-box {
    display: block;
    margin-left: -15px;
}

.edit-cell {
    min-height: 35px;
    cursor: pointer;
}
</style>